.edge-header-vertical-closed {
    .menu-item .second {
        @include edgeTransition(opacity .1s);
    }
    .menu-item .second.edge-float-open {
        margin-left: 0px;
        opacity: 1;
        visibility: visible;
        overflow: visible;
        @include edgeTransition(opacity .25s ease-out);
    }
}